'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { registerUser } from '@/services/api'
import { Box, Button, TextField, Typography } from '@mui/material'

export default function RegisterPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const router = useRouter()

  const handleRegister = () => {
    const result = registerUser(email, password)
    if (!result.success) {
      setError(result.message)
    } else {
      router.push('/login')
    }
  }

  return (
    <Box display="flex" flexDirection="column" maxWidth={400} margin="auto" gap={3} mt={10}>
      <Typography variant="h5">Registrieren</Typography>

      <TextField label="E-Mail" value={email} onChange={(e) => setEmail(e.target.value)} />
      <TextField label="Passwort" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />

      {error && <Typography color="error">{error}</Typography>}

      <Button variant="contained" onClick={handleRegister}>
        Registrieren
      </Button>

      {/* ➡️ Neuer Button */}
      <Button variant="outlined" onClick={() => router.push('/login')}>
        Schon einen Account? Einloggen
      </Button>
    </Box>
  )
}
